:root {
  --section-width     : calc(100vw - 30px);
  --section-gap-top   : 20px;
  --section-gap-bottom: 20px;
}


.container {
  width    : 100%;
  max-width: var(--section-width);
  margin   : 0 auto;
}



dialog,
section,
header,
footer {
  // padding: 0px calc(50% - var(--section-width) / 2);
  padding: 0px 15px;
}



@media (min-width: $lg) {

  dialog,
  section,
  header {
    padding: 0px 40px;
  }
}


